import React from "react";
import styled from "styled-components";

const Box = styled.div`
  /* border: 1px solid #ebebeb; */
  align-content: ${props => props.alignContent || null};
  align-items: ${props => props.alignItems || null};
  align-self: ${props => props.alignSelf || null};
  flex-direction: ${props => props.flexDirection || "row"};
  height: ${props =>
    typeof props.height === "number" ? props.height + "px" : props.height};
  justify-content: ${props => props.justifyContent || null};
  padding-left: ${props => props.paddingX * 8}px;
  padding-right: ${props => props.paddingX * 8}px;
  padding-top: ${props => props.paddingY * 8}px;
  padding-bottom: ${props => props.paddingY * 8}px;
  position: ${props => props.position || null};
  width: ${props =>
    typeof props.width === "number" ? props.width + "px" : props.width};
  display: ${props => props.display || "flex"};
  flex-wrap: ${props => props.flexWrap || null};
  flex: ${props => props.flex || null};
  background-color: ${props => props.backgroundColor};
  /* flex-grow: ${props => props.flexGrow};
  flex-shrink: ${props => props.flexShrink || 0};
  flex-basis: ${props => props.flexBasis || 1}; */
`;

export default Box;



