import React from "react";
import styled from "styled-components";
import get from "lodash/get";
import omit from "lodash/omit";

class Text extends React.Component {
  render() {
    const { as = "div", children, className, ...props } = this.props;
    const DynamicTag = as;
    return (
      <DynamicTag className={className} {...omit(props, ["light"])}>
        {children}
      </DynamicTag>
    );
  }
}

const regular = 400;
const semibold = 600;

const spToRem = pt => `${pt / 16}rem`;

const typo = {
  title1: {
    fontSize: spToRem(34),
    fontWeight: semibold
  },
  title2: {
    fontSize: spToRem(20),
    fontWeight: regular
  },
  bodyEmphasized: {
    fontSize: spToRem(17),
    fontWeight: semibold
  },
  body: {
    fontSize: spToRem(17),
    fontWeight: regular
  },
  callout: {
    fontSize: spToRem(16),
    fontWeight: regular
  },
  subhead: {
    fontSize: spToRem(15)
  },
  subheadEmphasized: {
    fontSize: spToRem(15),
    fontWeight: semibold
  },
  caption: {
    fontSize: spToRem(11),
    fontWeight: regular
  },
  captionEmphasized: {
    fontSize: spToRem(11),
    fontWeight: semibold
  }
};

const StyledText = styled(Text)`
  font-size: ${props => get(typo, [props.type, "fontSize"], "15px")};
  font-weight: ${props => get(typo, [props.type, "fontWeight"], regular)};
  color: ${props => (props.light ? "white" : "inherit")};
  text-transform: ${props => (props.transform ? props.transform : null)};
`;

export default StyledText;

export const Heading = ({ children, ...props }) => (
  <StyledText {...props} type="heading">
    {children}
  </StyledText>
);

export const Title = ({ children, ...props }) => (
  <StyledText {...props} type="section">
    {children}
  </StyledText>
);

export const Subtitle = ({ children, ...props }) => (
  <StyledText {...props} type="subsection">
    {children}
  </StyledText>
);

export const Body = ({ children, ...props }) => (
  <StyledText {...props} type="body">
    {children}
  </StyledText>
);

export const Caption = ({ children, ...props }) => (
  <StyledText {...props} type="meta">
    {children}
  </StyledText>
);



